body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.container{color:#c9ccea;display:flex;flex-direction:column;align-items:center;justify-content:center;min-height:100vh;background:#272a55;background:radial-gradient(circle, #1e2140 0%, #151932 65%)}.container .main-header{color:#06E087}.container small{color:white}.container h1{font-size:2.6rem;margin-bottom:0.4rem}.container .labels{list-style:none;display:flex;text-transform:uppercase;font-size:0.8rem;padding:0.3rem 0.3rem;background-color:#0c0e1b;border-radius:1.5rem}.active-label{color:#0c0e1b;background-color:#fe6f6b}.timer-container{display:flex;flex:2;align-items:center;justify-content:center}.time-wrapper{display:flex;align-items:center;justify-content:center;height:15rem;width:15rem;border-radius:7.5rem;color:#efefef;background:#151932;font-size:3.5rem;box-shadow:1rem 1.5rem 2rem rgba(0,0,0,0.6)}.button-wrapper{display:flex;align-items:center;justify-content:center;padding:2rem}button{color:#efefef;font-size:1rem;padding:0.5rem 1.2rem;font-weight:bold;border:none;border-radius:3rem;margin:0.2rem;background:#0c0e1b;min-width:80px;cursor:pointer}.button-wrapper button.activve{color:#0c0e1b;background-color:#c9ccea;border:1px solid #0c0e1b}.form-container{text-align:center}.input-wrapper{margin:1.5rem}button:hover{background-color:#fe6f6b;color:white;transition:all .09s ease-in-out}.form-container{width:500px;user-select:none}.form-container p{font-weight:bold;background-color:#32333c;display:inline;padding:2px 5px;border-radius:5px}.timer-box{display:flex;flex-direction:column;align-items:center;color:#ffffff}.timer-box .active-timer{text-transform:uppercase;font-size:1.2rem}
